body {
  margin: 0;
  padding: 0;
 font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: left;
  height: 100%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.App-buttons{
    background: #282c34;
    color: white;    
    padding: 5%;
    width: 50%;
    height: 50%;
}

.nav-button {
    background-color: #282c34;
    color: white;
    padding: 1%;
    width: 33%;
    display: block;
    margin: auto;
    text-align: center;
    text-decoration: none;
    font-size: calc(10px + 2vmin);
  }
.nav{
  background-color: #d9d9d9;
  padding: 1%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
}
.nav-button:focus{
  background-color: #a6cb12;
  color: black
}
.logo{
  width:20%;
  display:block;
}
html, body, #root {
    width: 100%;
    height: 100%;
  }
  
  .SplitPane {
    width: 100%;
    height: 100%;
  }
  
  .SplitPane-left {
    float: left;
    width: 30%;
    height: 100%;
    background: grey;
  }
  
  .SplitPane-right {
    float: left;
    width: 70%;
    height: 100%;
    background: lightgray;
   
  }
  
 .rightFlex{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
 }

  
.buttonsMenu{
    background: #282c34;
    color: white;    
    padding: 5%;
    width: 100%;
    height: 10%;
    font-size: 20px;
}
.Client {
    background-color: #4d515a;
    color: white;
    width: 100%;
    padding: 1%;
    text-align: center
}
.buttonFood{
    background: #282c34;
    color: white;    
    padding: 5%;
    width: 50%;
    height: 50%;
    font-size: 20px;
    margin-top: 5%;
}
.buttonDay{
    background: #282c34;
    color: white;    
    padding: 4%;
    width: 25%;
    height: 25%;
    max-height: 85px;
    font-size: 14px;
    display: block;
}

.buttonDay:hover {
    background-color: #a6cb12;
    color: black
  }

.buttonFood:hover{
    background-color: #a6cb12;
    color: black
}
.orderKitchen{
    border: 2px solid;
    padding: 2%;
    
}
